.bd-designMode-true .navbar {
  position: absolute;
}

.lp-navbar-advanced .profile-info {
  height: 50px;
}

.lp-navbar-advanced .profile-info > li {
  background: #080808;
}

.lp-navbar-advanced .profile-info .card {
  line-height: 50px;
}

.lp-navbar-advanced .profile-info .card .info .title a {
  color: #e6e6e6;
}

.lp-navbar-advanced .profile-info .card .info .name {
  color: #9d9d9d;
}

.lp-navbar-advanced .profile-info .card .logout {
  background: #eaaf1b;
  color: #FFF;
}

.lp-navbar-advanced .sign-in {
  height: 50px;
}

.lp-navbar-advanced .sign-in > li {
  height: 100%;
  line-height: 50px;
}

.lp-navbar-advanced .sign-in .link {
  display: inline-block;
  white-space: nowrap;
  background: #e7e7e7;
  color: #555;
  font-weight: bold;
}

.lp-navbar-advanced .sign-in .link:hover {
  background: #f4f4f4;
  color: #555;
}

.lp-navbar-advanced .navbar-static-top,
.bd-designMode-true .navbar {
  left: 0;
  right: 0;
  border-bottom: 0;
}

.lp-navbar-advanced .icon-container {
  text-align: center;
  float: left;
  height: 50px;
}

.lp-navbar-advanced .icon-container:before {
  content: '';
  position: relative;
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.lp-navbar-advanced .nav-icon {
  color: #fff;
  vertical-align: middle;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul {
  background: #f8f8f8;
  border-bottom: 1px solid #e7e7e7;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a {
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  -webkit-transition: background 0.08s;
  -moz-transition: background 0.08s;
  -o-transition: background 0.08s;
  transition: background 0.08s;
  -webkit-transition: all 0.08s;
  -moz-transition: all 0.08s;
  -o-transition: all 0.08s;
  transition: all 0.08s;
  min-height: 40px;
  color: #777;
  display: block;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a:hover {
  background: #ebebeb;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a:hover a {
  color: #333;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a:active {
  background: #d2d2d2;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a:active a {
  color: #555;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a.active {
  background: #e7e7e7;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a.active a {
  color: #555;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a.active:hover {
  background: #dbdbdb;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a.active:hover a {
  color: #555;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a.active:active {
  background: #cecece;
}

.lp-navbar-advanced .lp-navbar-advanced-root > li > ul > li a.active:active a {
  color: #555;
}

